
#banner {
  height: 100%;
  position: relative;
  z-index: 999;
  overflow: hidden;

  .frommd
  {
    display: none;
  }


  .homeBanner {
    height: 100%;
    width: 100%;
    max-width: 100%;
    display: block;
    left: 0;
    right: 0;
    z-index: 100;
  }

  .homeBanner .banner-bg {
    background: url("../img/headerbg.png") top center no-repeat;
    background-size: 100% 100%;
    display: block;
    height: 120%;
    width: 100%;
    position: relative;
    z-index: 100;
  }

  #images {
    max-width: 100%;
    overflow: hidden;
  }

  .images img {
    position: absolute;
    left: 0;
    bottom: 0;
    max-width: 100%;
  }

  .images img#img2, .images img#img2-mob{
    left: 0;
    right: 0;
    bottom: 0;
    top: auto;
    width: 150%;
    z-index: 120;
  }

  .images img#img2-mob
  {
    position: fixed;
    bottom: 0;
  }

 .images img#img1 {
    left: 0;
    bottom: -10px;
    top: auto;
    height: 25%;
    z-index: 120;
  }

  #bgvid {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 50;
    width: 100%;
    max-height: 100%;
    height: 100%;
    background: black;
  }

  #bgvid video {
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    bottom: 0;
  }

  @keyframes arrowAnimation {
    0% {top: 30px;}
    50% {top: 50px;}
    100% {top: 30px;}
  }

  p#scroll-msg {
      position: absolute;
      width: 100%;
      bottom: auto;
      top: 200px;
      text-align: center;
      font-size: 20px;
      font-weight: bold;
      color: white;
      left: 0px;
      text-shadow: 0px 0px 14px rgba(0, 0, 0, 1);   
  }

  p#scroll-msg i {
    position: absolute;
    top: 20px;
    animation-name: arrowAnimation;
    animation-duration: 2s;
    animation-iteration-count: infinite;
  }
}

@media (min-width: 768px)
{
#banner .images img#img1 {
  height: 52%;
}
}


@media (min-width: 992px)
{

  #banner .frommd
  {
    display: block;
  }

  #banner .images img#img1 {
    left: 0;
    height: 70%;
    z-index: 120;
  }

  @keyframes arrowAnimationDesk {
        0% {top: 60px;}
        50% {top: 100px;}
        100% {top: 60px;}
      }

    #banner
    {
      p#scroll-msg {
          position: absolute;
          width: 100%;
          top: 200px;
          bottom: auto;
          text-align: center;
          font-size: 60px;
          font-weight: bold;
          color: white;
          left: 0px;
          text-shadow: 0px 0px 14px rgba(0, 0, 0, 1);   
      }

      p#scroll-msg i {
        position: absolute;
        top: 60px;
        animation-name: arrowAnimationDesk;
        animation-duration: 2s;
        animation-iteration-count: infinite;
      }
    }

    #banner .images img#img2 {
    width: 85%;
    left: auto;
    right: 0;
    bottom: 0;
  }

  #banner #bgvid video {
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
  }

  @keyframes arrowAnimationVid {
    0% {top: 0px;}
    50% {top: 15px;}
    100% {top: 0px;}
  }

  #bgvid .scroll-msg-vid {
      position: absolute;
      width: 100%;
      bottom: auto;
      top: 100px;
      text-align: right;
      font-size: 20px;
      font-weight: bold;
      color: white;
      left: 0px;
      z-index: 100;
      text-shadow: 0px 0px 14px rgba(0, 0, 0, 1);

      p{
        display: inline-block;
        position: relative;
        font-size: 30px;
        padding-right: 40px;
      }
  }

  #bgvid .scroll-msg-vid i {
    position: absolute;
    top: 0px;
    left: -60px;
    animation-name: arrowAnimationVid;
    animation-duration: 1s;
    animation-iteration-count: infinite;
  }


}

@media (min-width: 1200px)
{
  #banner .images img#img1 {
    height: 70%;
  }

  #banner .images img#img2 {
    width: 90%;
    bottom: -10px;
  }

  #banner
  {
    p#scroll-msg 
    {
      top: 100px;
    }
  }

}

@media (min-width: 1440px)
{
  #banner .images img#img1 {
    height: 90%;
  }
}

