#message
{

  position: relative;
  padding-bottom: 0px;
  background-color: white;

  .mssg-bg
  {
    // background-color: white;
    padding-bottom: 100px;
    background: #ffffff; /* Old browsers */
    background: -moz-linear-gradient(top, #ffffff 0%, #ffffff 3%, #ffffff 38%, #ffffff 38%, #ffffff 85%, #b2dce8 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #ffffff 0%,#ffffff 3%,#ffffff 38%,#ffffff 38%,#ffffff 85%,#b2dce8 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #ffffff 0%,#ffffff 3%,#ffffff 38%,#ffffff 38%,#ffffff 85%,#b2dce8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#b2dce8',GradientType=0 ); /* IE6-9 */
  }

  @keyframes unfold
  {
    0% {transform: perspective(600px) rotateX(-90deg); bottom: -180px;}
    100% {transform: perspective(600px) rotateX(00deg); bottom: -3px;}
  }

  @keyframes unfoldBushes
  {
    0% {transform: perspective(600px) rotateX(-90deg); bottom: -180px;}
    100% {transform: perspective(600px) rotateX(00deg); bottom: -5px;}
  }

  @keyframes unfoldBack
  {
    0% {transform: perspective(600px) rotateX(00deg); bottom: -3px;}
    100% {transform: perspective(600px) rotateX(-90deg); bottom: -180px;}
  }

  @keyframes unfoldBushesBack
  {
    0% {transform: perspective(600px) rotateX(00deg); bottom: -5px;}
    100% {transform: perspective(600px) rotateX(-90deg); bottom: -180px;}
  }

  .mssg-header
  {
    position: relative;
    height: 100%;
    background-color: white;

    img
    {
      width: 100%;
    }

    .mssg-animation
    {

      img
      {
        position: absolute;
        bottom: 0;
      }
      
      #bush1, #bush2
      {
        width: 15%;
        bottom: -5px;
        animation-name: unfoldBushesBack;
        animation-duration: 1.0s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
      }

      #bush2
      {
        width: 25%;
        right: 0;
      }

      #building
      {
        width: 85%;
        left: 0;
        right: 0;
        bottom: -3px;
        margin: 0 auto;
        animation-name: unfoldBack;
        animation-duration: 0.8s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        visibility: unset;
      }

      &.animate
      {
        #bush1, #bush2
        {
          animation-name: unfoldBushes;
          animation-duration: 1.0s;
          animation-iteration-count: 1;
        }

        #building
        {
          animation-name: unfold;
          animation-duration: 0.8s;
          animation-iteration-count: 1;
        }
      }

    }

    #requests
    {
      position: absolute;
      top: 20px;
      bottom: auto;
      left: 0;
      right: 0;
      text-align: center;
      width: 100%;

      h1
      {
        font-size: 20px;
        color: white;
        text-shadow: 1px 1px black;
        text-align: center;
        font-weight: bold;
        display: block;

        &#count
        {
          font-size: 22px;
          display: inline-block;
          background-color: #162642;
          padding: 5px 20px;
          border-radius: 10px; 
        }
      }
    }
  }

  .mssg-content
  {
    margin-top: 100px;

    .mssg-title
    {
      margin-bottom: 35px;

      p
      {
        font-weight: bold;
        font-size: 23px;
        color: white;
        padding: 25px 50px;
        background-color: #162642;
        border-radius: 20px;
      } 
    }

    .mssg-text{
      p{
        font-weight: bold;
      }
    }

  }
  
}

@media (min-width: 992px)
{
  #message
  {
    .mssg-header
    {
      #requests
      {
        position: absolute;
        top: 150px;
        bottom: auto;
        left: 0;
        right: 0;
        text-align: center;
        width: 100%;

        h1
        {
          font-size: 45px;
          color: white;
          text-shadow: 1px 1px black;
          text-align: center;
          font-weight: bold;
          display: block;

          &#count
          {
            font-size: 60px;
            display: inline-block;
            background-color: #162642;
            padding: 15px 60px;
            border-radius: 10px; 
          }
        }
      }
    }

    .mssg-content
    {
      margin-top: 100px;

      .mssg-title
      {
        margin-bottom: 35px;

        p
        {
          font-weight: bold;
          font-size: 30px;
          color: white;
          padding: 25px 50px;
          background-color: #162642;
          border-radius: 20px;
        } 
      }

      .mssg-text{
        p{
          font-weight: bold;
        }
      }
    }


  }
}