#back-to-top{
  
  position: absolute;
  bottom: 25px;
  z-index: 9999;
  left: 0;
  right: 0;
  text-align: center;
  width: 100%;

  a{
    color: white;
    font-size: 22px;
    text-transform: capitalize;
    font-weight: bold;
    text-shadow: 1px 1px black;
  }
}
.loader {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: url("../img/loading-background.jpg") 50% 50% no-repeat rgb(255, 255, 255);
  background-size: cover;
  opacity: 1;
  h1{
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 3em;
    color: rgba(249, 5, 5, 1);
    font-style: italic;
    font-weight: bold;
  }
    
  .Fliplogo{    
    height: 169px;
    width: 169px;
    
    position: absolute;    

    top: calc(50% - 85px);
    left: 0;
    right: 0;

    margin-left: auto;
    margin-right: auto;

    perspective: 1000px;

    &-inner{
        height: 100%;
        width: 100%;

        position: relative;

        transform-style: preserve-3d;
        animation: loading-rotatingLogo 2s ease-in-out infinite;        
    }

    &-front, &-back{
        backface-visibility: hidden;

        height: 100%;
        width: 100%;

        position: absolute;
    }

    &-back{
        //Rotating what would be the back side
        transform: rotateY(180deg);
    }
  }

  //Animations
  @keyframes loading-rotatingLogo{
    0%   { transform: rotateY(0deg);   }
    50%  { transform: rotateY(180deg); }
    100% { transform: rotateY(360deg); }
  }

}
