.modal-backdrop.in
{
	position: relative;
}

#gallerySection {
	background-image: url("../img/background-gallery.jpg");
	background-size: cover;
	background-position: center;
	height: auto;
	width: 100%;
	margin: 0 auto;
	padding: 20px 0;
	position: relative;
	// margin-bottom: 150px;
}

.galleryContainer {
	/*background-color: rgb(255, 255, 255);
	width: 90%;*/
	margin: 0 auto;
	height: auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	padding: 20px;
	box-sizing: border-box;
	align-content: flex-start;
}

body .galleryContainer div {
	/*width: 33.33%;*/
	height: 300px;
	position: relative;
	background-color: rgba(0, 0, 0, 0.4);
	transition: ease-in 0.6s;
	z-index: 0;
}

body .galleryContainer div a {
	display: inline-block;
	width: 100%;
	height: 100%;
}

body .galleryContainer div:hover {
	transition: ease-out 0.6s;
	transform: scale(1.2);
	z-index: 1; 
}

.my_Close {
	font-size: 50px;
	opacity: 0.4;
}

#frst-img {
	clip-path: polygon(0% 0%, 100% 0%, 100% 30%, 85% 80%, 35% 100%, 35% 70%, 0% 80%);
}
#scnd-img {
	clip-path: polygon(20% 0%, 100% 0%, 80% 30%, 100% 50%, 80% 95%, 60% 80%, 10% 70%, 20% 30%);
	margin: 0 -5%;
}
#thrd-img {
	clip-path: polygon(20% 0%, 100% 0%, 100% 0%, 100% 100%, 75% 80%, 5% 80%, 20% 50%, 0% 30%);
}
#fth-img {
	clip-path: polygon(0% 15%, 30% 5%, 30% 40%, 85% 15%, 100% 85%, 55% 100%, 0% 80%);
	margin: -9% 0;
}
#fve-img {
	clip-path: polygon(5% 10%, 10% 5%, 60% 15%, 95% 40%, 100% 100%, 100% 100%, 25% 80%, 20% 85%);
 	margin: -9% -5%;
}
#six-img {
	clip-path: polygon(0% 25%, 5% 15%, 75% 15%, 100% 40%, 100% 100%, 100% 80%, 20% 100%, 15% 40%);
    margin: -9% 0;
}
#svn-img {
	clip-path: polygon(0% 15%, 55% 35%, 95% 20%, 100% 75%, 90% 100%, 0% 100%);
    margin: 0;
}
#eit-img {
	clip-path: polygon(15% 20%, 25% 15%, 95% 35%, 90% 80%, 100% 100%, 20% 100%, 10% 100%, 20% 75%);
    margin: 0 -5%;
}
#nin-img {
	clip-path: polygon(15% 40%,  100% 15%, 100% 100%, 20% 100%, 10% 80%);
    margin: 0;
}
