	}
	#fth-img:hover {
		background-image: url("../img/galleryImage4.png");
		background-size: cover;
	    background-position: center top 15px;
	}
	#fve-img:hover {
		background-image: url("../img/galleryImage5.png");
		background-size: cover;
	    background-position: center top;
	}
	#six-img:hover {
		background-image: url("../img/galleryImage6.png");
		background-size: cover;
	    background-position: center right;
	}
	#svn-img:hover {
		background-image: url("../img/galleryImage7.png");
		background-size: cover;
	    background-position: right -65px top 60px;
	}
	#eit-img:hover {
		background-image: url("../img/galleryImage8.png");
		background-size: cover;
