#testimonials {
  padding-top: 11450px;
  position: relative;
  overflow: hidden;



  .main-container {
    font: 16px/1.5 "Raleway", Helvetica, Helvetica Neue, Arial, sans-serif;
  }

  .wrapper {
    width: auto;
    margin: 0 10%;
  }

  /* Make the body 100% of the browser viewport height */
  .main-container {
    height: 100%;
    margin: 0;
  }

  .header-letter {
    margin-top: 0;
    font-family: "Roboto Condensed", Helvetica, Helvetica Neue, Arial,
      sans-serif;
    font-weight: 700;
    border-style: solid;
    text-align: center;
    border-width: 6px;
  }

  /* =Navigation */
  #navigation {
    // display: none;
    position: absolute;
    left: -60px;
    top: 0;
    text-align: center;
    z-index: 100;
    background-color: black;
    opacity: 0.8;
    height: 100%;
    width: 50px;
    padding-right: 2%;
    transition: all ease 0.5s;

    &.show-nav
    {
      // display: block;
      left: 0;

      &.hide-nav
      {
        // display: none;
        left: -60px;
      }
    }

    ul 
    {
      margin-left: 30px;
      position: fixed;
      top: 0;

      li a
      {
        width: 25px;
        height: 25px;

        span.nav-dot
        {
          height: 25px;
          width: 25px;
        }
      }
      

      li.active a
      {

        width: 25px;
        height: 25px;

        span
        {
          background-color: rgba(0,0,0,0);
          background: url('../img/stands.jfif') center center no-repeat;
          background-size: cover;
          height: 25px;
          width: 25px;
        }
      }
    }
  }
  #navigation a {
    line-height: 25px;
    position: relative;
    padding-right: 3px;
    color: #ffffff;
    text-decoration: none;
  }
  #navigation a:hover .nav-label {
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
  }
  #navigation .nav-dot {
    display: block;
    width: 10px;
    height: 10px;
    position: absolute;
    right: 0;
    top: 50%;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 100%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
  }
  #navigation .nav-label {
    display: block;
    opacity: 0.5;
    visibility: hidden;
    -webkit-transform: translateX(-10px);
    -ms-transform: translateX(-10px);
    transform: translateX(-10px);
    -webkit-transition: all 0.2s cubic-bezier(0.17, 0.67, 0.83, 0.67);
    transition: all 0.2s cubic-bezier(0.17, 0.67, 0.83, 0.67);
  }
  #navigation .is-dark a {
    color: #212121;
  }
  #navigation.is-dark .nav-dot {
    background-color: rgba(0, 0, 0, 0.5);
  }

  #navigation li {
    width: auto;
    list-style: none;
    padding-top: 130px;
  }

  /* =Main */
  .font-text p {
    font-size: 21px;
  }

  .slide {
    color: #ffffff;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    // padding-left: 220px;1
  }
  .slide .wrapper {
    padding-top: 45px;
    text-align: center;
    max-width: 95%;
    // width: 50%;
  }

  .slide#slide01 {
    background: #40739e;
  }
  .slide#slide02 {
    background: #34495e;
  }
  .slide#slide03 {
    background: #487eb0;
  }
  .slide#slide04 {
    background: #2c3e50;
  }

  /* =Simple animation up */
  .slideInUp {
    visibility: hidden;
    opacity: 0;
    -webkit-transition: all 1.5s ease-out;
    transition: all 1.5s ease-out;
    -webkit-transform: translate3d(0, 50px, 0);
    transform: translate3d(0, 50px, 0);
  }
  .is-active .slideInUp {
    visibility: visible;
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  .image-testimonial {
    width: 80%;
    border-radius: 40px;
    position: relative;
    padding-top: 25px;
    text-align: center;
  }

  .image-testimonial img {
    max-width: 80%;
    margin: 0 auto;
    max-height: 80%;
  }
  .img-border {
    border-radius: 40px;
    border: 7px solid;
  }

  #img-height {
    width: 26%;
    margin-left: 7%;
  }

  #header-first {
    font-size: 30px;
  }

  #header-second {
    font-size: 24px;
    font-weight: 700;
  }
}

@supports (-webkit-overflow-scrolling: touch) {     
    #testimonials
    {
      padding-top: 10800px;
    }
}

@media (min-width: 768px) {

  #testimonials {
     padding-top: 11100px;

     

    .slide {
      padding: 0px;
      flex-wrap: wrap;
    }
    .image-testimonial {
      margin-top: 35px;
      width: 80%;
    }
    .image-testimonial + .wraper {
      margin-bottom: 35px;
    }

    .slide .wrapper {
      width: 100%;
    }
    #img-height {
      width: 30%;
      
      img
      {
        max-height: 80%;
        width: 60%;
      }
    }

    @supports (-webkit-overflow-scrolling: touch) {     
      padding-top: 10100px;
      .slide .wrapper
      {
        width: auto;
      }
    }
  }

}

