@media (min-width: 992px) {
  #testimonials {
    padding-top: 10400px;

    @supports (-webkit-overflow-scrolling: touch) {
      padding-top: 9660px;
    }

    .slide {
	    color: #ffffff;
	    position: relative;
	    display: flex;
	    justify-content: center;
	    align-items: center;
	    flex-direction: row;
	    padding-left: 220px;

	    .wrapper
	    {
	    	width: 50%;
	    }
  	}

    .font-text p {
      font-size: 17px;
    }

    .slide {
      padding-left: 8%;
    }

    .image-testimonial {
      width: 55%;
      padding-top: 0;
      margin-top: 0;
    }
  }

  #testimonials {
    .slide{
        flex-wrap: nowrap;
    }
    .fs {
      height: 606px;
    }
  }


}

@media (min-width: 1200px) {
  #testimonials {

    #navigation
    {
      width: 50px;
    }

    padding-top: 9800px;
    .slide{
        flex-wrap: nowrap;
    }
    .fs {
      height: 606px;
    }
  }
}
