
@import "_variables";


//GLOBAL CHANGES

html{
	height: 100%;
}
body {
	font-family:$main-font;
	font-size:$main-font-size;
	color:$secondary-font-color;
	// word-wrap: break-word;
	background-color: white;
	height: 100%;
	overflow: hidden;
}
h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
	font-family:$main-font;
}
h1, .h1 {
	color:$main-font-color;
	text-transform:capitalize;
    font-family: $main-bold-font;
	font-size: $h1-size;
}
h2, .h2 {
	color:$main-font-color;
	font-weight: bold;
	font-size: $h2-size;
}
h3, .h3 {
	font-family: $main-font;
    font-weight: 400;
    font-style: normal;
    letter-spacing: .05em;
    text-transform: none;
	font-size: $h3-size;
}
p:selection,
p:-moz-selection {
	background-color: $main-color;
	color:white;
}
a {
	color:$main-color;
	@mixin states {
		color:tint($main-color, 30%);
		//color:$secondary-font-color;
		text-decoration:none;
	}

	&:hover,
    &:focus,
    &:active,
    &:active:focus,
    &:active:hover {
		@include states;
    }
}


