$color-background:transparent;

$snow-near: 10s;
$snow-mid: ($snow-near * 2);
$snow-far: ($snow-near * 3);

$ease-out-quad: cubic-bezier(.25,.46,.450,.94);


%on-top {
  z-index: 100;
  pointer-events: none;
}


.winter-is-coming {
  @extend %on-top;
  overflow: hidden;
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  max-width: 100%;
  background: $color-background;
}


.snow {
  @extend %on-top;
  position: absolute;
  top: 0; right: 0; bottom: 0; left: 0;
  -webkit-animation: falling linear infinite both;
  animation: falling linear infinite both;
  -webkit-transform: translate3D(0,-100%,0);
  transform: translate3D(0,-100%,0);

  &--near {
    -webkit-animation-duration: $snow-near;
    animation-duration: $snow-near;
    background-image: url("../img/snow-large.png");
    background-size: contain;

    & + .snow--alt {
      -webkit-animation-delay: ($snow-near / 2);
      animation-delay: ($snow-near / 2);
    }
  }

  &--mid {
    -webkit-animation-duration: $snow-mid;
    animation-duration: $snow-mid;
    background-image: url("../img/snow-medium.png");
    background-size: contain;

    & + .snow--alt {
      -webkit-animation-delay: ($snow-mid / 2);
      animation-delay: ($snow-mid / 2);
    }
  }

  &--far {
    -webkit-animation-duration: $snow-far;
    animation-duration: $snow-far;
    background-image: url("../img/snow-small.png");
    background-size: contain;

    & + .snow--alt {
      -webkit-animation-delay: ($snow-far / 2);
      animation-delay: ($snow-far / 2);
    }
  }
}

@-webkit-keyframes falling {
  0% {
    -webkit-transform: translate3D(-7.5%,-100%, 0);
    transform: translate3D(-7.5%,-100%, 0);
  }

  100% {
    -webkit-transform: translate3D(7.5%,100%, 0);
    transform: translate3D(7.5%,100%, 0);
  }
}

@keyframes falling {
  0% {
    -webkit-transform: translate3D(-7.5%,-100%, 0);
    transform: translate3D(-7.5%,-100%, 0);
  }

  100% {
    -webkit-transform: translate3D(7.5%,100%, 0);
    transform: translate3D(7.5%,100%, 0);
  }
}
