@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800');
@import url('https://fonts.googleapis.com/css?family=Raleway:100|Roboto+Condensed:700');

// COLORS //////////////////////////////

$main-color: #B7D433;
$secondary-color: #292A2C;
$tertiary-color:#675883;
$gray-color: #F2F5FA;
$footer-top:#757575;
$divisor-color:#e5e5e5;
$hover-color:#292A2C;
$link-color:#959595;
$internal-bgcolor:transparent;
$jrp-color:#F2F2F2;
$jdp-color:white;

$navbar-color: $secondary-color;


// FONTS ///////////////////////////////

$main-font: 'Open sans';
$main-bold-font: 'Open sans';
$main-italic-font:'Open sans';
$secondary-font:  'Open sans';
$tertiary-font:  'Open sans';

$main-font-size:16px;
$main-font-color:white;
$secondary-font-color:#282828;
$tertiary-font-color: #bbb;
$links-color: white;

$h1-lgsize:75px;
$h1-size:50px;
$h2-size:38px;
$h3-size:24px;
$h4-size:18px;

// PROPERTIES //////////////////////////

$rounded:true;
$toggleclosebtn:true;
$main-radius:40px;
$main-padding:15px;

// MIXINS /////////////////////////////

@mixin radius($value) {
	border-radius:$value;
	-moz-border-radius:$value;
	-webkit-border-radius:$value;
	-ms-border-radius:$value;
}
@mixin shadow($value){
	-moz-box-shadow:$value;
	-webkit-box-shadow:$value;
	box-shadow:$value;
}
@function tint($color, $percentage) {
  @return mix(white, $color, $percentage);
}
@function shade($color, $percentage) {
  @return mix(black, $color, $percentage);
}
@mixin shape-before-bottom-left{
	border-left: 50vw solid transparent;
  -webkit-transform: translate(49.91vw);
  -ms-transform: translate(49.91vw);
  transform: translate(49.91vw);
}
@mixin shape-after-bottom-right{

  border-right: 50vw solid transparent;
}
@mixin shape-form1{
  z-index: 2;
  border-bottom: 3vw solid #fff;
  bottom: -1px;
  position: absolute;
  content: '';
  display: table;
}

@mixin shape-form2-before-top-left{
	border-left: 50vw solid transparent;
	-webkit-transform: translate(50vw);
	-ms-transform: translate(50vw);
	transform: translate(50vw);
}

@mixin shape-form2-after-top-right{
	border-right: 50vw solid transparent;
}