
*{
	margin: 0px;
	padding: 0px;
}

.Footer{
	background-color: #B2DCE8;
	overflow: hidden;
	padding-top: 60px;
	position: relative;
	margin-top: -5px;
	z-index: 1000;

	&.animationBg{
		background-position: 0 0;
		background-repeat: repeat-x;
		background-size: cover;
	}

	&-clouds{
		background-image: url('../img/footer-clouds.png');
		height: 133px;
		width: 100%;
		z-index: 12;
		animation: animatedClouds 80s linear infinite;
	}


	&-greetingsTitle, &-greetingsSubtitle{
		display: block;	
		color: #B3000C ;		
		text-align: center;
		text-shadow: 2px 1px white;
		background: transparent;

		.red-text
		{
			color: #00B32C ;	
		}
	}

	&-greetingsSubtitle{
		color: #B3000C ;
	}

	&-greetings{
		padding: 25px 15px;
	}

	&-greetingsTitle{
		font-size: 45px;
		font-weight: 700;
		background: transparent;
		margin-bottom: 20px;
	}

	&-greetingsSubtitle{
		font-size: 48px;
		background: transparent;
	}

	&-backplane{
		background-image: url("../img/footer-backplane.png");
		  height: 223px;
		  width: 100%;
		  margin-bottom: -192px;
		  z-index: 15;
		  -webkit-animation: animatedBackline 50s linear infinite;
          animation: animatedBackline 50s linear infinite;
	}

	&-treeline{
		background-image: url("../img/footer-treeline.png");
		  height: 189px;
		  width: 100%;
		/*   margin-bottom: -30px; */
		  -webkit-animation: animatedTreeline 30s linear infinite;
		          animation: animatedTreeline 30s linear infinite;
		  z-index: 16;
	}

	&-road{
		position: relative;
	}

	&-roadPaviment{
		background-image: url("../img/footer-road2.png");
		  height: 83px;
		  width: 100%;
		  -webkit-animation: animatedRoad 20s linear infinite;
		          animation: animatedRoad 20s linear infinite;
		  margin-bottom: -83px;
		  position: relative;
		  z-index: 17;
	}

	&-roadBushes{
		background-image: url("../img/footer-road1.png");
		  height: 146px;
		  width: 100%;
		  -webkit-animation: animatedRoad 5s linear infinite;
		          animation: animatedRoad 5s linear infinite;
		  position: relative;
		  z-index: 21;
	}

	&-bus{
		position: absolute;
		  -webkit-animation: animatedBus 1.25s linear infinite;
		          animation: animatedBus 1.25s linear infinite;
		  bottom: 50%;
		  left: 15px;
		  z-index: 20;
	}

	//Animation Keyframes
	@keyframes animatedClouds{
		0%   { background-position: 0 0; }
		100% { background-position: -1924px 0; }
	}

	@keyframes animatedBackline{
		0%   { background-position: 0 0; }
		100% { background-position: -1924px 0; }
	}

	@keyframes animatedTreeline{
		0%   { background-position: 0 0; }
		100% { background-position: -1924px 0; }
	}

	@keyframes animatedRoad{
		0%   { background-position: 0 0; }
		100% { background-position: -1924px 0; }
	}
	
	@keyframes animatedBus{
		0%	 { transform: translate3d(0, 0, 0);    }
		50%  { transform: translate3d(0, -15px, 0); }
		100% { transform: translate3d(0, 0px, 0); }
	}
}

@media (min-width: 768px)
{
	.Footer{


		&-treeline{
			background-image: url('../img/footer-treeline.png');
		}
	}
}

