

header {
	background: rgba(0,0,0,0.8);
    position: absolute;
    top: 0;
    z-index: 1000;
    left: 0;
    right: 0;
    margin: 0 auto;
	.navbar-default {
		border:none;
		margin-bottom:0;
		background-color:transparent;

		.navbar-brand {
			display:table;
			max-width:70%;
			height: 100%;

			span {
				display:table-cell;
				vertical-align:middle;
			}
			img {
				width: auto;
    			height: auto;
			}
		}
		.navbar-toggle {
			padding: 9px;
			width: 48px;
			height: 50px;
			border: none;
			.icon-bar {
				background-color:$main-font-color;
				height:3px;
				width: 30px;
			}
			&:hover,
			&:focus {
				background-color:$main-color;
				color:white;
				.icon-bar {
					background-color:white;
				}
			}

		}
		#nav-search-button{
			width: inherit;
    		padding: 0px;
			margin-right:8px;
			i{
				margin-right: 2px;
				font-size: 20px;
				color: $main-font-color;
			}
		}
		#nav-search-button:focus, #nav-search-button:active, #nav-search-button:hover{
			background-color: transparent;
		}
		
		.navbar-collapse {

			border:none;
			.navbar-nav {

				margin: 0px -15px;

				li {
					text-align: center;
					a {
						color:#fff;
						text-transform:capitalize;
						font-weight:bold;
						font-size: 14px;
						padding:$main-padding;
						border: 1px solid $secondary-color;
						border-bottom: none;
						background-color: $secondary-color;
						&:hover,
						&:focus {
							color:$main-color;
							background-color: white;
						}
					}
					&.active a {
						color:$main-color;
					}
				}
				li.active {
					a,
					a:hover {
						background-color: white;
						color:$main-color;
					}
					a:active {
						color:$main-color;
						background-color: white
					}
				}
				li:last-child a {
						// border-left: 1px solid #F1F1F1;
				}
				/*** added **/
				li.dropdown a {
					color:$main-font-color;
					background-color: $secondary-color;
				}
				li.dropdown.open ul.dropdown-menu li a {
					color:$main-font-color;
					background-color: $secondary-color;
				}
			    li.dropdown.open ul.dropdown-menu li a:hover {
					color:$main-color;
					background-color: white
				}
				/*** end added **/
			}
			.dropdown-menu{
				padding: 0px;
				li{
					display: block;
					a{
					    color:$main-color;
						padding: 14px 15px 14px 30px;
						
					}
					a:hover{
						background-color:white;
						color:$secondary-color;
					}
				}
				li:last-child {
					display:block;
				}
			}
	
		}
	}
}

/* Small devices (tablets, 768px and up) */
@media (min-width: 768px) {

	header {

		.navbar-default {
			padding-top: 0px;
			.navbar-header {
				// width:100%;
		
				.navbar-brand {
					width:auto;
					max-width: 100%;
					margin-left: 0px;
					margin-top: 10px;
					padding-left: 0px;
					img {
						width: auto;
						height: 100%;
						margin: 0 auto;
					}
				}
			}

			.navbar-collapse 
			{
			padding: 0px;
				.navbar-nav {
					width: 100%;
					margin-top: 0px;
					margin-bottom: 0px;
					height: auto;
					display: flex;
					flex-direction: row;
					align-items: flex-end;
					justify-content: flex-end;
					li {
						float:none;
						flex: 0 0 15%;
						text-align: center;
						a {
							text-transform: capitalize;
							font-weight: bold;
							background-color:inherit;
							border: none;
							padding: 5px 12px;
							padding-right: 0px;
							font-size: 16px;
							padding-top: 16px;
							padding-bottom: 16px;
							padding-left: 12px;
							padding-right: 12px;
						}
						a:hover, a:focus, a:active {
							text-decoration: none;
							color:$secondary-font-color;
							background-color:rgba(245, 245, 245, 0.7)
						}
					}
					li:last-child {
						display:inline-block;
					}
					li.active {
						a,
						a:hover {
							color:$secondary-font-color;
							background-color: rgba(245, 245, 245, 0.7)
						}
					}
					/*** added **/
					li.dropdown a{
						background-color: white;
						color:$main-font-color;
					}
					li.dropdown a:hover,li.dropdown.open a,{
						color:$main-font-color;
						background-color: #F5F5F5;
					}
					li.dropdown.open ul.dropdown-menu li a {
						color: $main-font-color;
						background-color: $secondary-color;
						text-transform: capitalize;
						font-weight: bold;
						font-size: 18px;
						padding-bottom: 10px;
  						  padding-top: 10px;
					}
					li.dropdown.open ul.dropdown-menu li a:hover {
						text-decoration: underline;
						background-color:transparent;
						color:$footer-top;
						text-decoration: none;
						
					}
					/*** end added **/
					.dropdown-menu{
						border: none;
						padding-bottom: 0px;
						border-radius: 0;
						background-color: $secondary-color;
						margin-left: 0px;
						li{
							margin: 5px;
							margin-top: 0px;
							margin-bottom: 0px;
							a{
								color:$secondary-color;
								padding: 15px 15px 15px 30px;
								margin-left: 0px;
								padding-left: 15px;
							}
							a:hover{
								text-decoration: underline;
								color:$secondary-color;
							}
						}
						li:hover{

						}
						li:last-child {
							display:block;
						}
					}
				}
			}
		}
	}

} /* Medium devices (desktops, 992px and up) */
@media (min-width: 992px) {
	header {
		.navbar-default {
			.navbar-header{
				width: auto;
			}
			.navbar-collapse{
				.navbar-nav{

					li{
						height:auto;
						a{
							// padding-top: 15px;
  							// padding-bottom: 15px;
						}
					}
				}
			}

		}
	}

}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
	header{
			.navbar-default{
				.navbar-collapse{
					.navbar-nav{
						li {
							a {
									margin-left: 15px;
									padding-left: 22px;
									padding-right: 22px;
									margin-left: 0px;
							}	
					}
					.dropdown-menu{
						margin-left: 15px;
					}
				} 
			} 
		} 
	}
}

	